.cell{
    width: 30%;
    height: 200px;
    padding: 15px;
    overflow: hidden;
    border-radius: 5px;
    background-color: red;
    margin: 0 2.5% 20px 2.5%;
}
.cell .cell-content{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.cell .cell-content h4 {
    font-size: 18px;
    font-weight: bold;
    color: #fff;
}
.cell .cell-content h5 {
    font-weight: bold;
    color: #fff;
}
.cell .cell-content i {
    font-size: 80px;
    color: #fff;
}
